* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    color: #E6E6E6;
    background-color: #141414;
    overflow-x: hidden;
    display: flex;
    justify-content: center;
}

.BodyWrap { 
    width: 100%;
    height: 100%;
    max-width: 1920px;
    position: relative;
}

h1 {
    font-family: aktiv-grotesk-condensed, sans-serif;
    font-weight: 900;
    font-style: normal;
    text-transform: uppercase;
}

ul {
    list-style-type: none; 
}

a {
    text-decoration: none;
    color: #E6E6E6;
}

.GroundZero {
    width: 100%;
    height: 100%;
}

/* --- Global ------------ */







/* --- Navigation ------------ */

        .Nav {
            width: 100%;
            height: 100px;
            position: fixed;
            top: 0;
            left: 0;
            padding: 0px 80px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            z-index: 10000;
        }

            .Logo {
            }

            .NavList {
                display: flex;
                justify-content: space-between;
                width: 180px;
            }

                .NavItem {
                }

                    .NavLink {
                        color: #E6E6E6;
                        font-size: 18px;
                        font-weight: 200;
                    }






/* --- Portfolio Page ------------- */
.Portfolio {
    width: 100%;
    height: 100%;
    position: relative;
}

            .WindowWrap {
                width: 100%;
                height: 100vh;
                position: fixed;
                top: 0;
                left: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                z-index: 10;
            }

            .Window {
                width: 65%;
                height: 400px;
                background-color: #141414;
                box-shadow: 5px 5px 12px rgba(0, 0, 0, 0.297);
            }

    .PortfolioContent {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 20;
    }

        .PortfolioTitle {
            margin-top: 80px;
            font-size: 80px;
            text-align: center;
        }

        .PortfolioProjects {
            width: 100%;
            margin-left: 50px;
        }
















/* --- About Page ------------- */

.About {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: cadetblue;
}